<template>
  <div class="container">
    <page-title first-title="限时抢购"></page-title>
    <div class="search-wrapper">
      <el-form :inline="true">
        <el-row :gutter="20" style="margin-right: 0;">
          <el-form-item label="活动名称">
            <el-input placeholder="活动名称" clearable v-model="params.secbuy_name"></el-input>
          </el-form-item>
          <el-form-item label="创建时间">
            <el-date-picker
              type="datetime"
              placeholder="开始时间"
              v-model="params.create_start_time"
              value-format="timestamp"
            ></el-date-picker>
            <span>&nbsp;&nbsp;~&nbsp;&nbsp;</span>
            <el-date-picker
              type="datetime"
              placeholder="结束时间"
              v-model="params.create_end_time"
              value-format="timestamp"
            ></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addNewActivity" icon="el-icon-plus">
              新增
            </el-button>
            <!-- <el-button type="primary" @click="addNewEnroll">
              发布报名
            </el-button> -->
          </el-form-item>
        </el-row>
        <el-row :gutter="20" style="margin-right: 0;">
          <el-form-item label="活动状态">
            <el-select clearable placeholder="活动状态" v-model="params.secbuy_status">
              <el-option
                v-for="status in statusList"
                :key="status.status_id"
                :label="status.status_name"
                :value="status.status_id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="活动时间">
            <el-date-picker
              type="datetime"
              placeholder="开始时间"
              v-model="params.start_time"
              value-format="timestamp"
            ></el-date-picker>
            <span>&nbsp;&nbsp;~&nbsp;&nbsp;</span>
            <el-date-picker
              type="datetime"
              placeholder="结束时间"
              v-model="params.end_time"
              value-format="timestamp"
            ></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button icon="el-icon-search" @click="getActivityList">
              搜索
            </el-button>
          </el-form-item>
        </el-row>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table
        :data="activityList"
        stripe
        v-loading="loading"
        style="width: 100%"
        :header-cell-style="{ background: '#eef1f6' }"
      >
        <el-table-column prop="secbuy_id" label="序号"></el-table-column>
        <!-- <el-table-column label="序号" type="index"></el-table-column> -->
        <el-table-column prop="secbuy_name" label="活动名称" min-width="450px"></el-table-column>
        <el-table-column label="活动时间" min-width="300px">
          <template slot-scope="scope">
            <span>{{ timeStampFmt(scope.row, 'start_time') }}</span>
            &nbsp;&nbsp;至&nbsp;&nbsp;
            <span>{{ timeStampFmt(scope.row, 'end_time') }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="secbuy_status" label="活动状态" :formatter="statusFmt"></el-table-column>
        <el-table-column
          prop="created_at"
          label="创建时间"
          min-width="180px"
          :formatter="timeStampFmt"
        ></el-table-column>
        <el-table-column label="操作" min-width="200px">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="goDetail(scope.row.secbuy_id)">
              查看
            </el-button>
            <!-- <el-tooltip class="item" effect="dark" content="未开始状态只能编辑明天及以后的活动" placement="top-start"> -->
            <el-button
              type="text"
              size="small"
              @click="goEdit(scope.row)"
              v-if="scope.row.secbuy_status === 0 || scope.row.secbuy_status === 1 || scope.row.secbuy_status === 3"
            >
              编辑
            </el-button>
            <!-- </el-tooltip> -->
            <el-button type="text" size="small" @click="goForbid(scope.row)" v-if="scope.row.secbuy_status === 0">
              禁用
            </el-button>
            <!-- <el-button type="text" size="small" @click="goStart(scope.row)" v-if="scope.row.secbuy_status === 3">
							开启
						</el-button> -->
            <el-button
              type="text"
              size="small"
              @click="goRemove(scope.row.secbuy_id)"
              v-if="scope.row.secbuy_status === 0 || scope.row.secbuy_status === 3"
            >
              移除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 分页 -->
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      background
      layout="prev, pager, next, jumper"
      :total="total"
      :hide-on-single-page="true"
    >
    </el-pagination>
  </div>
</template>

<script>
/**
 * 时间格式化方法：时间戳，后台到s级别的，前端获取到的为ms
 * 状态名称转换方法
 */
import { STATUS } from './static'
import { ActivityList, DeleteActivity, ForbidActivity, OpenActivity } from '../../api/secondkill'
export default {
  name: 'SecondKill',
  data() {
    return {
      params: {
        pageNumber: 1,
        pageSize: 10,
        secbuy_name: '',
        start_time: '',
        end_time: '',
        create_start_time: '',
        create_end_time: '',
        secbuy_status: ''
      },
      currentPage: 1,
      total: 0,
      activityList: [],
      statusList: STATUS,
      loading: null,
      isStart: false // 通过开启进入编辑页面，不受时间限制
    }
  },
  created() {
    this.getActivityList()
  },
  computed: {
    canEdit() {}
  },
  methods: {
    // 列表
    getActivityList() {
      this.loading = true
      let _p = JSON.parse(JSON.stringify(this.params))
      for (let key in _p) {
        if (key.endsWith('_time') && _p[key]) {
          _p[key] = this.utils.formatStamp(_p[key])
        }
      }
      ActivityList(_p).then(res => {
        this.activityList = res.data
        this.total = res.total
        this.loading = false
      })
    },
    // 新增活动
    addNewActivity() {
      this.$router.push({ name: 'SecondKillAdd', query: { operate: 'add' } })
    },
    // 查看
    goDetail(id) {
      this.$router.push({ name: 'SecondKillDetail', params: { id: id } })
    },
    // 编辑
    goEdit(row) {
      // 今天结束时间
      /*let todayEnd = new Date(new Date().toLocaleDateString()).getTime() + 24 * 60 * 60 * 1000 - 1
			if (!this.isStart && row.secbuy_status === 0) {
				if (row.start_time * 1000 < todayEnd) {
					this.$message.error('未开始状态只能编辑明天及以后的活动')
					return
				}
			}*/
      this.$router.push({ name: 'SecondKillAdd', query: { id: row.secbuy_id, status: row.secbuy_status } })
    },
    // 禁用
    goForbid(row) {
      this.$confirm('确定禁用本场活动吗', '提示').then(() => {
        ForbidActivity(row.secbuy_id).then(res => {
          if (res) {
            row.secbuy_status = 3
            this.$message.success('禁用成功')
          }
        })
      })
    },
    // 移除
    goRemove(id) {
      this.$confirm('确定移除该活动吗？', '提示').then(() => {
        this.loading = true
        // 移除可售库存变动，调取java接口
        this.$api.delete(`hd/itzSecbuyGoods/batch?secbuy_id=${id}`, null, r => {
          if (r.status == 200) {
            DeleteActivity(id).then(res => {
              if (res) {
                this.getActivityList()
              }
            })
          }
        })
      })
    },
    // 开启
    goStart(row) {
      this.$confirm('确定开启本场活动吗？', '提示').then(() => {
        let curTime = parseInt(this.utils.formatStamp(new Date().getTime()))
        if (row.start_time <= curTime) {
          this.$confirm('活动开始时间需大于当前时间，请修改活动时间', '提示').then(() => {
            this.isStart = true
            this.goEdit(row)
          })
        } else {
          OpenActivity(row.secbuy_id).then(res => {
            row.secbuy_status = 0
          })
        }
      })
    },
    // 格式化时间戳
    timeStampFmt(row, column) {
      let key = typeof column === 'string' ? column : column.property
      let _t = row[key]
      return _t ? this.utils.formatDate('YYYY-MM-DD HH:mm:ss', this.utils.formatStamp(_t, 1)) : _t
    },
    statusFmt(row, column) {
      let status = row[column.property]
      return STATUS.map(function(item) {
        return item.status_id === status ? item.status_name : void 0
      })
    },
    // 页面切换
    handleCurrentChange() {
      this.params.pageNumber = this.currentPage
      this.getActivityList()
    },
    //秒杀二期  发布报名活动  2019-10-8
    addNewEnroll() {
      this.$router.push({ name: 'addNewEnroll' })
    }
  }
}
</script>

<style lang="scss" scoped>
.search-wrapper {
  margin-top: 15px;
}
.el-pagination {
  margin-top: 10px;
  text-align: center;
}
</style>
