<template>
  <div class="container">
    <!-- <page-title first-title="运营活动管理" second-title="限时促销"></page-title> -->
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>有解商城管理中心</el-breadcrumb-item>
        <el-breadcrumb-item>运营活动管理</el-breadcrumb-item>
        <el-breadcrumb-item>限时促销</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="search-wrapper">
      <el-form :inline="true">
        <el-row :gutter="20" style="margin-right: 0;">
          <el-form-item label="活动名称">
            <el-input placeholder="活动名称" clearable v-model="params.secbuy_name"></el-input>
          </el-form-item>
          <el-form-item label="创建时间">
            <el-date-picker
              type="datetime"
              placeholder="开始时间"
              v-model="params.create_start_time"
              value-format="timestamp"
            ></el-date-picker>
            <span>&nbsp;&nbsp;~&nbsp;&nbsp;</span>
            <el-date-picker
              type="datetime"
              placeholder="结束时间"
              v-model="params.create_end_time"
              value-format="timestamp"
            ></el-date-picker>
          </el-form-item>
          <!-- <el-form-item>
            <el-button type="primary" @click="addNewActivity" icon="el-icon-plus">
              创建活动
            </el-button>
          </el-form-item> -->
        </el-row>
        <el-row :gutter="20" style="margin-right: 0;">
          <el-form-item label="活动状态">
            <el-select clearable placeholder="活动状态" v-model="params.secbuy_status">
              <el-option
                v-for="status in statusList"
                :key="status.status_id"
                :label="status.status_name"
                :value="status.status_id"
              ></el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item label="活动时间">
            <el-date-picker
              type="datetime"
              placeholder="开始时间"
              v-model="params.start_time"
              value-format="timestamp"
            ></el-date-picker>
            <span>&nbsp;&nbsp;~&nbsp;&nbsp;</span>
            <el-date-picker
              type="datetime"
              placeholder="结束时间"
              v-model="params.end_time"
              value-format="timestamp"
            ></el-date-picker>
          </el-form-item> -->
          <el-form-item label="活动形式">
            <el-select clearable filterable placeholder="活动形式" v-model="params.type">
              <el-option v-for="type in typeList" :key="type.id" :label="type.name" :value="type.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" icon="el-icon-search" @click="getActivityList">
              搜索
            </el-button>
            <el-button icon="el-icon-refresh" @click="clearSearchParams">
              清空
            </el-button>
            <el-button type="primary" @click="addNewActivity" icon="el-icon-plus">
              创建活动
            </el-button>
          </el-form-item>
        </el-row>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table
        :data="activityList"
        stripe
        border
        v-loading="loading"
        style="width: 100%"
        :header-cell-style="{ background: '#eef1f6' }"
      >
        <el-table-column prop="secbuy_id" label="编号"></el-table-column>
        <el-table-column
          prop="secbuy_name"
          label="活动名称"
          min-width="200%"
          :show-overflow-tooltip="true"
        ></el-table-column>
        <el-table-column prop="type" label="活动形式" :formatter="fmtType"></el-table-column>
        <el-table-column label="活动时间" min-width="300%" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span>{{ timeStampFmt(scope.row, 'start_time') }}</span>
            &nbsp;&nbsp;至&nbsp;&nbsp;
            <span>{{ timeStampFmt(scope.row, 'end_time') }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="created_at"
          label="创建时间"
          min-width="180px"
          :formatter="timeStampFmt"
          :show-overflow-tooltip="true"
        ></el-table-column>
        <el-table-column prop="secbuy_status" label="活动状态" :formatter="statusFmt"></el-table-column>
        <el-table-column label="操作" min-width="150%">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="goActivityUrl(scope.row.secbuy_id)">
              链接
            </el-button>
            <el-button type="text" size="small" @click="goDetail(scope.row.secbuy_id)">
              查看
            </el-button>
            <el-button
              type="text"
              size="small"
              @click="goEdit(scope.row)"
              v-if="scope.row.secbuy_status === 0 || scope.row.secbuy_status === 1 || scope.row.secbuy_status === 3"
            >
              编辑
            </el-button>
            <el-button
              type="text"
              size="small"
              @click="goRemove(scope.row.secbuy_id)"
              v-if="scope.row.secbuy_status === 0 || scope.row.secbuy_status === 3"
            >
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 分页 -->
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="params.pageNumber"
      background
      layout="prev, pager, next, jumper"
      :total="total"
      :hide-on-single-page="true"
    >
    </el-pagination>

    <!-- 弹框 -->
    <el-dialog title="活动链接" :visible.sync="showUrl" width="50%">
      <div class="url-info">{{ urlInfo }}</div>
      <div class="btn-box">
        <el-button @click="showUrl = false">取消</el-button>
        <el-button type="primary" v-clipboard:copy="urlInfo" v-clipboard:success="onCopy" v-clipboard:error="onError"
          >复制</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
/**
 * 时间格式化方法：时间戳，后台到s级别的，前端获取到的为ms
 * 状态名称转换方法
 */
import { STATUS, ACTIVITY_TYPE } from './static'
import { ActivityList, DeleteActivity, ForbidActivity, OpenActivity } from '../../api/secondkill'
export default {
  name: 'SecondKill',
  data() {
    return {
      params: {
        pageNumber: 1,
        pageSize: 10,
        secbuy_name: '',
        start_time: '',
        end_time: '',
        create_start_time: '',
        create_end_time: '',
        secbuy_status: '',
        type: '',
        activity_type: 1
      },
      total: 0,
      activityList: [],
      statusList: STATUS,
      typeList: ACTIVITY_TYPE,
      loading: false,
      showUrl: false,
      urlInfo: '' // 活动链接
    }
  },
  created() {
    this.getActivityList()
  },
  computed: {
    fmtParams() {
      let _p = JSON.parse(JSON.stringify(this.params))
      for (let key in _p) {
        if (key.endsWith('_time') && _p[key]) {
          _p[key] = this.utils.formatStamp(_p[key])
        }
      }
      if (_p.type === '') {
        _p.type = 0
      }
      return _p
    }
  },
  methods: {
    // 列表
    getActivityList() {
      this.loading = true
      ActivityList(this.fmtParams)
        .then(res => {
          this.activityList = res.data
          this.total = res.total
        })
        .finally(() => {
          this.loading = false
        })
    },
    // 清空搜索条件
    clearSearchParams() {
      for (let key in this.params) {
        this.params[key] = ''
      }
      this.params.pageNumber = 1
      this.params.pageSize = 10
      this.getActivityList()
    },
    // 新增活动
    addNewActivity() {
      this.$router.push({ name: 'PromoteSaleAdd', query: { operate: 'add' } })
    },
    // 查看
    goDetail(id) {
      this.$router.push({ name: 'PromoteSaleDetail', params: { id: id } })
    },
    // 编辑
    goEdit(row) {
      this.$router.push({ name: 'PromoteSaleAdd', query: { id: row.secbuy_id, status: row.secbuy_status } })
    },
    // 链接
    goActivityUrl(id) {
      this.urlInfo = 'https://m.huanhuanyiwu.com/h5/#/promotionList/' + id
      this.showUrl = true
    },
    onCopy(e) {
      this.$message.success('复制成功')
      this.showUrl = false
    },
    onError(e) {
      this.$message.error('复制失败')
    },
    // 移除
    goRemove(id) {
      this.$confirm('确定删除该活动吗？', '提示').then(() => {
        this.loading = true
        DeleteActivity(id).then(res => {
          if (res) {
            this.$message.success('删除成功')
            this.getActivityList()
          }
        })
      })
    },
    // 格式化时间戳
    timeStampFmt(row, column) {
      let key = typeof column === 'string' ? column : column.property
      let _t = row[key]
      return _t ? this.utils.formatDate('YYYY-MM-DD HH:mm:ss', this.utils.formatStamp(_t, 1)) : _t
    },
    fmtType(row, column, cellValue) {
      let _n = ''
      this.typeList.forEach(item => {
        if (item.id == cellValue) {
          _n = item.name
        }
      })
      return _n
    },
    statusFmt(row, column, cellValue) {
      let _n = ''
      this.statusList.forEach(item => {
        if (item.status_id == cellValue) {
          _n = item.status_name
        }
      })
      return _n
    },
    // 页面切换
    handleCurrentChange() {
      this.getActivityList()
    }
  }
}
</script>

<style lang="scss" scoped>
.search-wrapper {
  margin-top: 15px;
}
.url-info {
  text-align: center;
  margin-bottom: 20px;
}
.btn-box {
  text-align: center;
}
.el-pagination {
  margin-top: 10px;
  text-align: center;
}
</style>
