<template>
  <div class="container" v-loading.fullscreen.lock="loading">
    <!-- title -->
    <page-title first-title="编辑商品"></page-title>
    <!-- content -->
    <div class="good-info-wrapper">
      <el-form label-width="120px" :rules="goodRules" :model="good_infos" ref="goodForm">
        <el-form-item label="商品名称" prop="goods_name">
          <el-input :disabled="notEdit" v-model="good_infos.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="商品编号" prop="goods_id">
          <el-input :disabled="notEdit" v-model="good_infos.goods_id"></el-input>
        </el-form-item>
        <el-form-item label="所属店铺" prop="shop_name">
          <el-input :disabled="notEdit" v-model="good_infos.shop_name"></el-input>
        </el-form-item>
        <el-form-item label="秒杀结算价" prop="secbuy_close_price">
          <el-input v-model="good_infos.secbuy_close_price" clearable></el-input>
          <span>注：请输入数字(下同)</span>
        </el-form-item>
        <el-form-item label="秒杀价" prop="shop_price">
          <el-input v-model="good_infos.shop_price" clearable></el-input>
          <div class="price-wrapper">
            <p>
              <label>现金价</label>
              <el-input v-model="good_infos.cash_price" clearable></el-input>
            </p>
            <p>
              <label>积分支付上限</label>
              <!-- <el-input v-model="good_infos.huanbi_price" :disabled="notEdit"></el-input> -->
              <el-input v-model="good_infos.huanbi_price" :disabled="notEdit"></el-input>
            </p>
          </div>
        </el-form-item>
        <!-- <el-form-item label="秒杀支付类型" prop="token_type">
          <el-select clearable v-model="good_infos.token_type">
            <el-option v-for="type in paytype" :key="type.type_id" :label="type.type_name" :value="type.type_id">
            </el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="原价" prop="goods_shop_price">
          <el-input :disabled="notEdit" v-model="good_infos.goods_shop_price"></el-input>
          <div class="price-wrapper">
            <p>
              <label>现金价</label>
              <el-input :disabled="notEdit" v-model="good_infos.goods_cash"></el-input>
            </p>
            <p>
              <label>积分支付上限</label>
              <el-input :disabled="notEdit" v-model="good_infos.goods_money_line"></el-input>
            </p>
          </div>
        </el-form-item>
        <!-- <el-form-item>
          <el-radio
            disabled
            v-model="current_token_type"
            v-for="type in paytype"
            :key="type.type_id"
            :label="type.type_id"
            >{{ type.type_name }}</el-radio
          >
        </el-form-item> -->
        <el-form-item label="可售库存" prop="goods_number">
          <el-input :disabled="notEdit" v-model="good_infos.goods_number"></el-input>
        </el-form-item>
        <el-form-item label="秒杀库存" prop="secbuy_quantity">
          <el-input v-model="good_infos.secbuy_quantity" clearable></el-input>
        </el-form-item>
        <el-form-item label="每人限购" prop="max_quantity">
          <el-input v-model="good_infos.max_quantity"></el-input>
        </el-form-item>
        <el-form-item label="排序值" prop="goods_order">
          <el-input v-model="good_infos.goods_order" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveGood">确定</el-button>
          <el-button @click="goBack">返回</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import { SaveGoodsInfo, GetGoods } from '../../api/secondkill'
// import { PAYTYPE } from './static'
export default {
  name: 'KillGoodEdit',
  data() {
    // 自定义规则
    // 活动限购数量，不能大于库存
    let vSecbuyQuantity = (rule, value, callback) => {
      if (value) {
        if (Number(value) > Number(this.good_infos.goods_number)) {
          callback(new Error('活动限购数量不得大于库存'))
        } else {
          callback()
        }
      }
    }
    // 每人限购
    let vMaxQuantity = (rule, value, callback) => {
      if (this.good_infos.secbuy_quantity && Number(value) > Number(this.good_infos.secbuy_quantity)) {
        callback(new Error('每人限购不得大于活动限购数量'))
      } else {
        callback()
      }
    }
    // 秒杀价 >= 现金价
    let vCash = (rule, value, callback) => {
      if (value != '' && this.good_infos.cash_price != '' && Number(value) < Number(this.good_infos.cash_price)) {
        callback(new Error('秒杀价需大于等于现金价'))
        this.good_infos.shop_price = ''
        this.good_infos.cash_price = ''
      } else {
        callback()
      }
    }
    // 是否是数字
    let vNumber = (rule, value, callback) => {
      if (value) {
        if (isNaN(value)) {
          callback(new Error('请输入数字'))
        } else {
          callback()
        }
      }
    }
    return {
      // paytype: PAYTYPE,
      notEdit: true, // 可编辑
      good_infos: {},
      goodRules: {
        secbuy_close_price: [
          { required: true, message: '秒杀结算价必填', trigger: 'blur' },
          { validator: vNumber, message: '请输入数字', trigger: 'blur' }
        ],
        shop_price: [
          { required: true, message: '秒杀价必填', trigger: 'blur' },
          { validator: vNumber, message: '请输入数字', trigger: 'blur' },
          { validator: vCash, trigger: 'blur' }
        ],
        secbuy_quantity: [
          { required: true, message: '活动限购数量必填', trigger: 'blur' },
          { validator: vSecbuyQuantity, trigger: 'blur' },
          { validator: vNumber, message: '请输入数字', trigger: 'blur' }
        ],
        max_quantity: [
          { required: true, message: '每人限购数量必填', trigger: 'blur' },
          { validator: vMaxQuantity, trigger: 'blur' },
          { validator: vNumber, message: '请输入数字', trigger: 'blur' }
        ],
        goods_order: [
          { required: true, message: '排序值必填', trigger: 'blur' },
          { validator: vNumber, message: '请输入数字', trigger: 'blur' }
        ]
      },
      current_token_type: '',
      loading: true,
      javaData: {
        ori_secbuy_quantity: '',
        secbuy_goods_id: this.$route.params.goodID ? this.$route.params.goodID : ''
      }
    }
  },
  created() {
    GetGoods(this.$route.params.goodID).then(res => {
      this.good_infos = res[0]
      this.current_token_type = res[0].token_type
      this.loading = false
      // java传值
      this.javaData.ori_secbuy_quantity = res[0].secbuy_quantity
    })
  },
  watch: {
    shop_cash_price(val) {
      this.good_infos.huanbi_price = Number(val.shop_price) - Number(val.cash_price)
    }
  },
  computed: {
    shop_cash_price() {
      let pri_obj = { shop_price: this.good_infos.shop_price, cash_price: this.good_infos.cash_price }
      return pri_obj
    }
  },
  methods: {
    saveGood() {
      this.$refs['goodForm'].validate(valid => {
        if (valid) {
          if (
            Number(this.good_infos.shop_price) !==
            Number(this.good_infos.cash_price) + Number(this.good_infos.huanbi_price)
          ) {
            this.$message.error('秒杀价为现金价与积分支付上限的和')
            return
          }
          console.log(this.javaData.ori_secbuy_quantity)
          console.log(this.good_infos.secbuy_quantity)
          if (this.javaData.ori_secbuy_quantity != Number(this.good_infos.secbuy_quantity)) {
            console.log(this.javaData)
            console.log(this.javaData.secbuy_goods_id)
            // 修改活动限购数量调取java接口
            this.$api.put(
              `hd/itzSecbuyGoods/secbuy/goods?secbuy_goods_id=${this.javaData.secbuy_goods_id}&ori_secbuy_quantity=${this.javaData.ori_secbuy_quantity}&secbuy_quantity=${this.good_infos.secbuy_quantity}`,
              null,
              r => {
                if (r.status == 200) {
                  SaveGoodsInfo(this.good_infos).then(
                    res => {
                      if (res) {
                        this.$message.success('保存成功')
                        this.goBack()
                      }
                    },
                    err => {
                      this.$message({
                        message: err.errorMsg,
                        type: 'error'
                      })
                      if (err.errorCode == '1001') {
                        this.good_infos.shop_price = ''
                      } else if (err.errorCode == '1002') {
                        this.good_infos.cash_price = ''
                      } else if (err.errorCode == '1003') {
                        this.good_infos.shop_price = ''
                      } else if (err.errorCode == '1004') {
                        this.good_infos.cash_price = ''
                      }
                    }
                  )
                }
              }
            )
          } else {
            SaveGoodsInfo(this.good_infos).then(
              res => {
                if (res) {
                  this.$message.success('保存成功')
                  this.goBack()
                }
              },
              err => {
                this.$message({
                  message: err.errorMsg,
                  type: 'error'
                })
                if (err.errorCode == '1001') {
                  this.good_infos.shop_price = ''
                } else if (err.errorCode == '1002') {
                  this.good_infos.cash_price = ''
                } else if (err.errorCode == '1003') {
                  this.good_infos.shop_price = ''
                } else if (err.errorCode == '1004') {
                  this.good_infos.cash_price = ''
                }
              }
            )
          }
        }
      })
    },
    goBack() {
      this.$router.replace({ name: 'SecondKillAdd', query: { id: this.$route.params.actID } })
    }
  }
}
</script>
<style lang="scss" scoped>
.good-info-wrapper {
  margin-top: 15px;

  .el-input {
    width: 220px;
  }

  .price-wrapper {
    p {
      width: 220px;
      display: flex;

      label {
        width: 150px;
      }
    }
  }

  span {
    margin-left: 20px;
    color: #999;
  }
}
</style>
