<template>
  <div class="container">
    <page-title first-title="活动详情"></page-title>
    <!-- 活动信息 -->
    <div class="activity-info-wrapper">
      <p>活动名称：{{ secbuy_info.secbuy_name }}</p>
      <p>活动时间：{{ `${fmtTime(secbuy_info.start_time)} ~ ${fmtTime(secbuy_info.end_time)}` }}</p>
    </div>
    <!-- 活动下属商品 -->
    <div class="goods-list-wrapper">
      <p>已绑定商品</p>
      <el-table
        :data="goods_info"
        stripe
        style="width: 100%"
        :header-cell-style="{ background: '#eef1f6' }"
        v-loading="loading"
      >
        <el-table-column label="序号" prop="id"></el-table-column>
        <el-table-column label="商品编号" prop="goods_id"></el-table-column>
        <el-table-column label="商品名称" prop="goods_name"></el-table-column>
        <el-table-column label="排序值" prop="goods_order"></el-table-column>
        <el-table-column label="可售库存" prop="goods_number"></el-table-column>
        <el-table-column label="活动限购数量" prop="secbuy_quantity"></el-table-column>
        <el-table-column label="每人限购" prop="max_quantity"></el-table-column>
        <el-table-column label="秒杀结算价" prop="secbuy_close_price" :formatter="fmtMoney"></el-table-column>
        <el-table-column label="原价">
          <template slot-scope="scope">
            <p>{{ utils.formatMoney(scope.row.goods_shop_price) }}</p>
            <p>
              {{
								`(现金价:${utils.formatMoney(scope.row.goods_cash)} + 积分支付上限:${utils.formatMoney(
                  scope.row.goods_money_line
                )})`
              }}
            </p>
          </template>
        </el-table-column>
        <el-table-column label="秒杀总价">
          <template slot-scope="scope">
            <p>{{ utils.formatMoney(scope.row.shop_price) }}</p>
            <p>
              {{
								`(现金价:${utils.formatMoney(scope.row.cash_price)} + 积分支付上限:${utils.formatMoney(
                  scope.row.huanbi_price
                )})`
              }}
            </p>
          </template>
        </el-table-column>
        <el-table-column label="秒杀支付类型" prop="token_type" :formatter="fmtPayType"></el-table-column>
        <el-table-column label="所属店铺" prop="shop_name"></el-table-column>
      </el-table>
    </div>
    <!-- 分页 -->
    <el-pagination
      :hide-on-single-page="true"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="params.pageSize"
      background
      layout="prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
    <div class="btn-wrapper">
      <el-button @click="$router.go(-1)">返回</el-button>
    </div>
  </div>
</template>

<script>
import { ActivityDetail } from '../../api/secondkill'
import { PAYTYPE } from './static'
export default {
  name: 'SecondKillDetail',
  data() {
    return {
      secbuy_info: [],
      goods_info: [],
      params: {
        secbuy_id: this.$route.params.id,
        pageNumber: 1,
        pageSize: 5
      },
      currentPage: 1,
      total: 0,
      loading: null
    }
  },
  created() {
    this.getActivityDetail()
  },
  methods: {
    getActivityDetail() {
      this.loading = true
      ActivityDetail(this.params).then(res => {
        this.secbuy_info = res.secbuy_info
        this.goods_info = res.list
        this.total = res.total
        this.loading = false
      })
    },
    // 页面切换
    handleCurrentChange() {
      console.log('current page is ', this.currentPage)
      this.params.pageNumber = this.currentPage
      this.getActivityDetail()
    },
    fmtTime(time) {
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', this.utils.formatStamp(time, '1'))
    },
    fmtMoney(row, column) {
      return this.utils.formatMoney(row[column.property])
    },
    fmtPayType(row, column) {
      let type = row[column.property]
      return PAYTYPE.map(function(item) {
        return item.type_id === type ? item.type_name : void 0
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.activity-info-wrapper {
  padding-left: 15px;
  font-size: 16px;
}
.goods-list-wrapper {
  background-color: #fff;
  padding: 15px;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 0;
}
.btn-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 15px;
}
.el-pagination {
  margin-top: 10px;
  text-align: center;
}
</style>
