<template>
  <div class="container" v-loading.fullscreen.lock="isLoading">
    <!-- title -->
    <!-- <page-title first-title="运营活动管理" :second-title="isAdd ? '新增活动' : '活动编辑'"></page-title> -->
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>有解商城管理中心</el-breadcrumb-item>
        <el-breadcrumb-item>运营活动管理</el-breadcrumb-item>
        <el-breadcrumb-item>
          {{ isAdd ? '新增活动' : '活动编辑' }}
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- activity info -->
    <div class="activity-info-wrapper">
      <el-form ref="activityForm" label-width="120px" :rules="addRules" :model="activity_infos">
        <el-form-item label="活动名称" prop="secbuy_name">
          <el-input
            placeholder="请输入活动名称"
            clearable
            v-model="activity_infos.secbuy_name"
            :disabled="isDisable"
          ></el-input>
          <span class="tip-info">最多20个字符</span>
        </el-form-item>
        <el-form-item label="活动形式">
          <p><el-radio v-model="activity_infos.type" :label="1" :disabled="createandimport">积分专享</el-radio></p>
          <p>
            <el-radio v-model="activity_infos.type" :label="2" :disabled="createandimport">限时折扣</el-radio>
          </p>
          <template v-if="activity_infos.type == 2">
            <p>
              <label class="el-form-item__label">折扣是否在前台显示</label>
              <el-switch
                v-model="activity_infos.is_show_discount"
                :active-value="1"
                :inactive-value="0"
                :disabled="isDisable"
              ></el-switch>
            </p>
            <p v-if="activity_infos.is_show_discount">
              <label class="el-form-item__label">折扣底色</label>
              <el-input
                placeholder="手动输入颜色，十六进制"
                v-model="activity_infos.discount_colour"
                clearable
                :disabled="isDisable"
              ></el-input>
              <colorPicker v-model="activity_infos.discount_colour" :disabled="isDisable" />
            </p>
          </template>
        </el-form-item>
        <el-form-item label="标签名称" prop="tags_name">
          <el-input
            placeholder="请输入标签名称"
            clearable
            v-model="activity_infos.tags_name"
            :disabled="isDisable"
          ></el-input>
          <span class="tip-info">最多5个字符</span>
        </el-form-item>
        <el-form-item label="标签底色" prop="tags_colour">
          <el-input
            placeholder="手动输入颜色，十六进制"
            v-model="activity_infos.tags_colour"
            clearable
            :disabled="isDisable"
          ></el-input>
          <colorPicker v-model="activity_infos.tags_colour" :disabled="isDisable" />
        </el-form-item>
        <el-form-item label="活动时间">
          <el-col :span="5">
            <el-form-item prop="start_time">
              <el-date-picker
                v-model="activity_infos.start_time"
                type="datetime"
                placeholder="活动开始时间"
                value-format="timestamp"
                default-time="00:00:00"
                :disabled="isDisable"
                style="width: 100%;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="1" style="text-align: center;">-</el-col>
          <el-col :span="5">
            <el-form-item prop="end_time">
              <el-date-picker
                v-model="activity_infos.end_time"
                type="datetime"
                placeholder="活动结束时间"
                value-format="timestamp"
                default-time="23:59:59"
                :disabled="isDisable"
                style="width: 100%;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="适用人群">
          <p><el-radio v-model="activity_infos.range" :label="0" :disabled="isDisable">换换全部用户</el-radio></p>
          <p><el-radio v-model="activity_infos.range" :label="1" :disabled="isDisable">换换新用户</el-radio></p>
          <p><el-radio v-model="activity_infos.range" :label="2" :disabled="isDisable">爱投资全部用户</el-radio></p>
        </el-form-item>
      </el-form>
    </div>
    <!-- goods list -->
    <div class="goods-list-wrapper" v-loading="submitLoading">
      <div class="import-good-wrapper">
        <span>*</span>
        <label>导入商品</label>
        <el-upload
          ref="uploadFile"
          :headers="importHeaders"
          :action="action"
          :data="fmtParams"
          name="myfile"
          :auto-upload="false"
          :on-success="uploadSuccess"
          :on-change="addFile"
          :before-remove="removeFile"
          :limit="1"
          :on-exceed="overFile"
        >
          <el-button type="primary" :disabled="isDisable">导入商品</el-button>
        </el-upload>
        <el-button class="tpl-download" type="text" @click="downloadTpl" :disabled="isDisable">模板下载</el-button>
      </div>
      <el-table
        :data="goods_info"
        stripe
        style="width: 100%"
        border
        :header-cell-style="{ background: '#eef1f6' }"
        v-loading="pageLoading"
      >
        <el-table-column label="序号" type="index" width="50"></el-table-column>
        <el-table-column label="商品ID" prop="goods_id"></el-table-column>
        <el-table-column
          label="商品名称"
          prop="goods_name"
          min-width="300%"
          :show-overflow-tooltip="true"
        ></el-table-column>
        <el-table-column
          label="所属商家"
          prop="shop_name"
          min-width="200%"
          :show-overflow-tooltip="true"
        ></el-table-column>
        <el-table-column label="可售库存" prop="goods_number"></el-table-column>
        <el-table-column label="活动限购数量" prop="secbuy_quantity" min-width="120%"></el-table-column>
        <el-table-column
          label="促销现金结算价"
          prop="secbuy_close_price"
          min-width="120%"
          :formatter="fmtMoney"
        ></el-table-column>
        <el-table-column
          label="促销积分结算价"
          prop="settlement_surplus"
          min-width="120%"
          :formatter="fmtMoney"
        ></el-table-column>
        <el-table-column label="原价" min-width="200%">
          <template slot-scope="scope">
            <p>{{ utils.formatMoney(scope.row.goods_shop_price) }}</p>
            <p>
              {{
                `(现金:${utils.formatMoney(scope.row.goods_cash)} + 积分:${utils.formatMoney(
                  scope.row.goods_money_line
                )})`
              }}
            </p>
          </template>
        </el-table-column>
        <el-table-column label="促销价" min-width="200%">
          <template slot-scope="scope">
            <p>{{ utils.formatMoney(scope.row.shop_price) }}</p>
            <p>
              {{
                `(现金:${utils.formatMoney(scope.row.cash_price)} + 积分:${utils.formatMoney(scope.row.huanbi_price)})`
              }}
            </p>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <div v-if="curStatus">
              <el-button type="text" size="small" @click="sellNone(scope.row)" :disabled="!scope.row.is_on_sale"
                >售罄</el-button
              >
            </div>
            <div v-else>
              <el-button type="text" size="small" @click="goEdit(scope.row.id)">
                编辑
              </el-button>
              <el-button type="text" size="small" @click="goRemove(scope.row.id)">
                移除
              </el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 分页 -->
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="pageNumber"
      background
      layout="prev, pager, next, jumper"
      :page-size="pageSize"
      :total="total"
    >
    </el-pagination>
    <!-- button groups -->
    <div class="btn-groups-wrapper">
      <el-button type="" @click="goback">取消</el-button>
      <el-button type="primary" @click="saveNewActivity" :disabled="notSave">确定</el-button>
    </div>

    <!-- 导入商品确认对话框 -->
    <div class="error-good-info-wrapper">
      <el-dialog :visible.sync="centerDialogVisible" width="50%" :show-close="false">
        <div slot="title">导入商品提示</div>
        <div>
          <div>
            <el-row :gutter="22" v-if="data_type.sale">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">已下架</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.sale }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.goods_number">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">已售罄</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.goods_number }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.sale_goods_number">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">售罄并下架</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.sale_goods_number }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.check_zero">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">未审核</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.check_zero }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.export_fail">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">导入失败</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.export_fail }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.param_error">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">数据错误</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.param_error }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.money_error">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">金额错误</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.money_error }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.sku_goods">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">多规格SKU</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.sku_goods }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.seckill_price">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">设置的秒杀价不能高于本店售价</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.seckill_price }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.seckill_casg">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">设置的秒杀现金价不能高于本店售价中的现金价</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.seckill_casg }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.shop_seckill_price">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">售价 - 促销价需大于等于 1</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.shop_seckill_price }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.shop_seckill_cash">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">售价现金价 - 促销现金价需大于等于 1</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.shop_seckill_cash }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.goods_name">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">商品编号与商品名称不一致，请修改后重新提交。</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.goods_name }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.discount_activity_shop_price">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">限时折扣活动价要低于本店售价。</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.discount_activity_shop_price }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.special_activity_shop_price">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">积分专享活动价不等于本店售价。</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.special_activity_shop_price }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.special_activity_money_line">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">积分专享抵扣上限小于默认权限抵扣上限。</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.special_activity_money_line }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.other_activity_running">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">已参与其他活动</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.other_activity_running }}</el-col>
            </el-row>
            <p>请仔细核对商品信息后重新上传失败数据</p>
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <!-- <el-button @click="centerDialogVisible = false">取 消</el-button> -->
          <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { SaveNewActivity, ActivityDetail, RemoveGoods, SellGoodsNone, downloadTemplate } from '../../api/secondkill'
import { saveAs } from 'file-saver'
import { PAYTYPE } from './static'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'SecondKillAdd',
  data() {
    // 自定义校验规则
    let checkEndDate = (rule, value, callback) => {
      if (value !== '') {
        if (this.activity_infos.start_time !== '' && value <= this.activity_infos.start_time) {
          callback(new Error('结束时间不能小于开始时间'))
          this.activity_infos.end_time = ''
        } else {
          callback()
        }
      }
    }
    // 只能创建明天及以后的活动
    let checkStartDate = (rule, value, callback) => {
      if (value !== '') {
        if (this.activity_infos.end_time !== '' && value >= this.activity_infos.end_time) {
          callback(new Error('活动时间不得大于结束时间'))
          this.activity_infos.start_time = ''
        } else {
          callback()
        }
      }
    }
    let token = null
    if (window.token) {
      token = window.token
    }
    if (process.env.NODE_ENV === 'development') {
      // admin uid=2 的永久token
      token =
        'eyJ0eXAiOiJKV1QiLCJhbGciOiJ' +
        'IUzI1NiJ9.eyJ1aWQiOjIsImV4c' +
        'CI6Mjg0MDExMjAwMCwicGxhdGZv' +
        'cm0iOiIifQ.x7sXrFHcDZUEiIkV' +
        'q2JDO1FQxpOsXXsq0qTaAKuGQNw'
    }
    return {
      action: process.env.VUE_APP_APPSERVER_HOST + '/admin/api.seckill.activity.add',
      importHeaders: {
        'X-ADMIN-Authorization': token,
        'X-HH-Ver': process.env.VUE_APP_APPSERVER_VERSION
      },
      hasFile: false,
      // 活动信息
      activity_infos: {
        secbuy_id: '',
        type: 1,
        secbuy_name: '',
        tags_name: '',
        tags_colour: '#000000',
        start_time: '',
        end_time: '',
        range: 0,
        discount_colour: '#000000',
        is_show_discount: 0
      },
      pageNumber: 1,
      pageSize: 5,
      total: 0,
      goods_info: [],
      // 导入信息提示
      data_type: {
        sale: '',
        goods_number: '',
        sale_goods_number: '',
        check_zero: '',
        export_fail: '',
        param_error: '',
        money_error: '',
        sku_goods: '',
        discount_activity_shop_price: '',
        special_activity_shop_price: '',
        special_activity_money_line: '',
        goods_name: '',
        other_activity_running: '',
        seckill_casg: '',
        seckill_price: '',
        shop_seckill_cash: '',
        shop_seckill_price: ''
      }, // 商品导入信息
      secbuy_infos: {}, // 活动信息
      // 校验规则
      addRules: {
        secbuy_name: [
          { required: true, message: '请输入活动名称', trigger: 'change' },
          { min: 3, max: 20, message: '活动名称长度3~20', trigger: 'change' }
        ],
        tags_name: [
          { required: true, message: '请输入标签名称', trigger: 'change' },
          { max: 5, message: '标签名称长度过长', trigger: 'change' }
        ],
        tags_colour: [{ required: true, message: '请选择标签颜色', trigger: 'change' }],
        start_time: [
          { required: true, message: '请选择开始时间', trigger: 'blur' },
          { validator: checkStartDate, trigger: 'blur' }
        ],
        end_time: [
          { required: true, message: '请选择结束时间', trigger: 'blur' },
          { validator: checkEndDate, trigger: 'blur' }
        ]
      },
      fullscreenLoading: true,
      submitLoading: false,
      pageLoading: false,
      notSave: false,
      centerDialogVisible: false
    }
  },
  computed: {
    isAdd() {
      return this.$route.query.operate === 'add'
    },
    curStatus() {
      return Number(this.$route.query.status) === 1
    },
    isDisable() {
      return !this.isAdd && this.curStatus
    },
    isValid() {
      let flag = false
      this.$refs['activityForm'].validate(valid => {
        flag = valid
      })
      return flag
    },
    isLoading() {
      return this.fullscreenLoading && !this.isAdd
    },
    fmtParams() {
      let p = JSON.parse(JSON.stringify(this.activity_infos))
      // p.is_show_discount = Number(p.is_show_discount)
      p.start_time = p.start_time / 1000
      p.end_time = p.end_time / 1000
      let pageNumber = this.pageNumber
      let pageSize = this.pageSize
      return { ...p, pageNumber, pageSize }
    },
    createandimport() {
      // 当创建活动并且导入商品后，则活动形式不可更改
      if (!this.isAdd && this.goods_info.length > 0) {
        return true
      }
      return false
    }
  },
  created() {
    // console.log(this.isAdd)
    if (!this.isAdd) {
      this.activity_infos.secbuy_id = this.$route.query.id
      this.getActivityInfos()
    }
  },
  methods: {
    getActivityInfos() {
      this.pageLoading = true
      ActivityDetail(this.fmtParams).then(res => {
        this.activity_infos = res.secbuy_info
        this.goods_info = res.list
        this.fullscreenLoading = false
        this.pageLoading = false
        this.total = res.total
        this.activity_infos.start_time = this.utils.formatStamp(this.activity_infos.start_time, 1)
        this.activity_infos.end_time = this.utils.formatStamp(this.activity_infos.end_time, 1)
      })
    },
    // 页面切换
    handleCurrentChange() {
      this.getActivityInfos()
    },
    // 编辑商品
    goEdit(id) {
      this.$router.replace({
        name: 'PromoteSaleGoodEdit',
        params: { actID: this.activity_infos.secbuy_id, goodID: id }
      })
    },
    // 移除
    goRemove(id) {
      this.$confirm('确定移除该商品吗', '提示').then(() => {
        this.pageLoading = true
        RemoveGoods(id).then(res => {
          if (res) {
            this.handleCurrentChange()
          }
        })
      })
    },
    // 售罄
    sellNone(row) {
      SellGoodsNone(row.id).then(res => {
        if (res) {
          this.$message.success('该商品下架成功')
          row.is_on_sale = 0
        }
      })
    },
    // 保存活动
    saveNewActivity() {
      if (this.isValid) {
        // 新增必须导入商品
        if (this.isAdd) {
          if (!this.hasFile) {
            this.$message.error('新增活动必须导入商品')
          } else {
            this.submitLoading = true
            this.$refs.uploadFile.submit()
          }
        } else {
          // 编辑保存时，判断是否有重新导入商品
          if (new Date().getTime() >= this.activity_infos.start_time) {
            this.$message.error('活动已经开始了')
            return
          }
          if (this.hasFile) {
            this.submitLoading = true
            this.$refs.uploadFile.submit()
          } else {
            this.submitLoading = true
            SaveNewActivity(this.fmtParams)
              .then(res => {
                if (res) {
                  this.$message.success('保存成功')
                }
              })
              .finally(() => {
                this.submitLoading = false
              })
          }
        }
      }
    },
    // 上传文件成功回调
    uploadSuccess(response) {
      this.submitLoading = false
      this.$refs.uploadFile.clearFiles()
      let _data = response.data
      this.data_type = _data.data.data_type
      for (let key in this.data_type) {
        if (this.data_type[key].length > 0) {
          this.centerDialogVisible = true
        }
        this.data_type[key] = this.data_type[key].join()
      }
      if (this.isAdd && !this.centerDialogVisible) {
        this.notSave = true
      }
      this.total = _data.total
      if (response.code || _data.list.length === 0) {
        this.$message.error('保存失败,请重新导入商品')
        this.hasFile = false
        return
      } else {
        this.$message.success('保存成功')
        this.goods_info = _data.list
        this.submitLoading = false
        this.activity_infos = _data.secbuy_info
        this.activity_infos.start_time = this.activity_infos.start_time * 1e3
        this.activity_infos.end_time = this.activity_infos.end_time * 1e3
      }
    },
    // 文件超出限制回调
    overFile() {
      this.$message.error('只能上传一个文件')
    },
    // 添加文件
    addFile(file) {
      // 添加文件、上传成功、失败都会调用该函数，只在添加文件时修改hasFile的状态
      if (file.status === 'ready') {
        this.hasFile = true
      }
    },
    // 移除文件
    removeFile() {
      this.hasFile = false
    },
    // 下载模板
    downloadTpl() {
      downloadTemplate().then(res => {
        saveAs(new Blob([res.data]), res.filename)
      })
    },
    // 钱格式化
    fmtMoney(row, column) {
      return this.utils.formatMoney(row[column.property])
    },
    // 支付方式转换
    fmtPayType(row, column) {
      let type = row[column.property]
      return PAYTYPE.map(function(item) {
        return item.type_id === type ? item.type_name : void 0
      })
    },
    goback() {
      this.$router.replace({ name: 'PromoteSaleList' })
    }
  }
}
</script>

