<template>
  <div class="container" v-loading.fullscreen.lock="isLoading">
    <!-- title -->
    <page-title :first-title="isAdd ? '新增活动' : '编辑活动'"></page-title>
    <!-- activity info -->
    <div class="activity-info-wrapper">
      <el-form ref="activityForm" label-width="120px" :rules="addRules" :model="activity_infos">
        <el-form-item label="活动名称" prop="secbuy_name">
          <el-input
            placeholder="请输入活动名称"
            clearable
            v-model="activity_infos.secbuy_name"
            :disabled="isDisable"
          ></el-input>
        </el-form-item>
        <el-form-item label="活动开始时间" prop="start_time">
          <el-date-picker
            v-model="activity_infos.start_time"
            type="datetime"
            placeholder="请选择活动开始时间"
            value-format="timestamp"
            default-time="00:00:00"
            :disabled="isDisable"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="活动结束时间" prop="end_time">
          <el-date-picker
            v-model="activity_infos.end_time"
            type="datetime"
            placeholder="请选择活动结束时间"
            value-format="timestamp"
            default-time="23:59:59"
            :disabled="isDisable"
          ></el-date-picker>
        </el-form-item>
      </el-form>
    </div>
    <!-- goods list -->
    <div class="goods-list-wrapper" v-loading="submitLoading">
      <div class="import-good-wrapper">
        <span>*</span>
        <label>关联商品</label>
        <el-upload
          ref="uploadFile"
          :headers="importHeaders"
          :action="action"
          :data="postParams"
          name="myfile"
          :auto-upload="false"
          :on-success="uploadSuccess"
          :on-change="addFile"
          :before-remove="removeFile"
          :limit="1"
          :on-exceed="overFile"
        >
          <el-button type="primary" :disabled="isDisable || mutex0">导入商品</el-button>
        </el-upload>
        <!-- <el-button type="primary" class="newbtn" @click="ChoiceOnline" :disabled="isDisable || mutex1"
          >线上选品</el-button
        > -->
        <el-button class="tpl-download" type="text" @click="downloadTpl" :disabled="isDisable">模板下载</el-button>
      </div>
      <el-table
        :data="goods_info"
        stripe
        style="width: 100%"
        border
        :header-cell-style="{ background: '#eef1f6' }"
        v-loading="pageLoading"
      >
        <template>
          <el-table-column v-if="!isProduct" label="序号" prop="id" width="50"></el-table-column>
          <el-table-column v-else label="序号" type="index" width="50"></el-table-column>
        </template>
        <el-table-column label="商品编号:" prop="goods_id"></el-table-column>
        <el-table-column label="商品名称" prop="goods_name"></el-table-column>
        <el-table-column label="排序值" prop="goods_order"></el-table-column>
        <el-table-column label="可售库存" prop="goods_number"></el-table-column>
        <el-table-column label="秒杀库存" prop="secbuy_quantity"></el-table-column>
        <!-- <el-table-column label="活动限购数量" prop="secbuy_quantity"></el-table-column> -->
        <el-table-column label="每人限购" prop="max_quantity"></el-table-column>
        <el-table-column label="秒杀结算价" prop="secbuy_close_price" :formatter="fmtMoney"></el-table-column>
        <el-table-column label="原价" width="200">
          <template slot-scope="scope">
            <p>{{ utils.formatMoney(scope.row.goods_shop_price) }}</p>
            <p>
              {{
                `(现金价:${utils.formatMoney(scope.row.goods_cash)} + 积分支付上限:${utils.formatMoney(
                  scope.row.goods_money_line
                )})`
              }}
            </p>
          </template>
        </el-table-column>
        <el-table-column label="秒杀总价" width="200">
          <template slot-scope="scope">
            <p>{{ utils.formatMoney(scope.row.shop_price) }}</p>
            <p>
              {{
                `(现金价:${utils.formatMoney(scope.row.cash_price)} + 积分支付上限:${utils.formatMoney(
                  scope.row.huanbi_price
                )})`
              }}
            </p>
          </template>
        </el-table-column>
        <!-- <el-table-column label="秒杀支付类型" prop="token_type" :formatter="fmtPayType"></el-table-column> -->
        <el-table-column label="所属店铺" prop="shop_name"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <div v-if="curStatus">
              <el-button type="text" size="small" @click="sellNone(scope.row)" :disabled="!scope.row.is_on_sale"
                >售罄</el-button
              >
            </div>
            <div v-else>
              <template v-if="!isProduct || isProduct & activity_infos.secbuy_id">
                <el-button type="text" size="small" @click="goEdit(scope.row.id)">
                  编辑
                </el-button>
                <el-button type="text" size="small" @click="goRemove(scope.row.id)">
                  移除
                </el-button>
              </template>
              <template v-else>
                <el-button type="text" size="small" @click="Removepro(scope.row.goods_id)">
                  移除
                </el-button>
              </template>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 分页 -->
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      background
      layout="prev, pager, next, jumper"
      :page-size="pageSize"
      :total="total"
    >
    </el-pagination>
    <!-- button groups -->
    <div class="btn-groups-wrapper">
      <el-button type="primary" @click="saveNewActivity" :disabled="notSave">确定</el-button>
      <el-button type="" @click="goback">返回</el-button>
    </div>

    <!-- 导入商品确认对话框 -->
    <div class="error-good-info-wrapper">
      <el-dialog :visible.sync="centerDialogVisible" width="50%" :show-close="false">
        <div slot="title">导入商品提示</div>
        <div>
          <div>
            <el-row :gutter="22" v-if="data_type.sale">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">已下架</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.sale }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.goods_number">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">已售罄</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.goods_number }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.sale_goods_number">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">售罄并下架</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.sale_goods_number }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.check_zero">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">未审核</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.check_zero }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.export_fail">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">导入失败</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.export_fail }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.param_error">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">数据错误</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.param_error }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.money_error">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">金额错误</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.money_error }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.sku_goods">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">多规格SKU</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.sku_goods }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.seckill_price">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">设置的秒杀价不能高于本店售价</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.seckill_price }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.seckill_casg">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">设置的秒杀现金价不能高于本店售价中的现金价</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.seckill_casg }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.shop_seckill_price">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">售价 - 秒杀价需大于等于 1</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.shop_seckill_price }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.shop_seckill_cash">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">售价现金价 - 秒杀现金价需大于等于 1</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.shop_seckill_cash }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.goods_name">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">商品编号与商品名称不一致，请修改后重新提交。</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.goods_name }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.discount_activity_shop_price">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">限时折扣活动价要低于本店售价。</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.discount_activity_shop_price }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.special_activity_shop_price">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">积分专享活动价不等于本店售价。</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.special_activity_shop_price }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.special_activity_money_line">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">积分专享抵扣上限小于默认权限抵扣上限。</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.special_activity_money_line }}</el-col>
            </el-row>
            <el-row :gutter="22" v-if="data_type.other_activity_running">
              <el-col :span="3">状态:</el-col>
              <el-col class="word-style" :span="9">已参与其他活动</el-col>
              <el-col :span="4">商品编号:</el-col>
              <el-col class="over-hid" :span="8">{{ data_type.other_activity_running }}</el-col>
            </el-row>
            <p>请仔细核对商品信息后重新上传失败数据</p>
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <!-- <el-button @click="centerDialogVisible = false">取 消</el-button> -->
          <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { AddNewActivity, ActivityDetail, RemoveGoods, SellGoodsNone, downloadTemplate } from '../../api/secondkill'
import { saveAs } from 'file-saver'
import { PAYTYPE } from './static'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'SecondKillAdd',
  data() {
    // 自定义校验规则
    let checkEndDate = (rule, value, callback) => {
      if (value !== '') {
        if (this.activity_infos.start_time !== '' && value <= this.activity_infos.start_time) {
          callback(new Error('结束时间不能小于开始时间'))
          this.activity_infos.end_time = ''
        } else {
          callback()
        }
      }
    }
    // 只能创建明天及以后的活动
    let checkStartDate = (rule, value, callback) => {
      if (value !== '') {
        // 取消只能创建明天及以后的活动的限制 2019.11.06
        // let todayEnd = new Date(new Date().toLocaleDateString()).getTime() + 24 * 60 * 60 * 1000
        /*if (value < todayEnd) {
          callback(new Error('只能创建或编辑明天及以后的活动'))
        } else */
        if (this.activity_infos.end_time !== '' && value >= this.activity_infos.end_time) {
          callback(new Error('活动时间不得大于结束时间'))
          this.activity_infos.start_time = ''
        } else {
          callback()
        }
      }
    }
    let token = null
    if (window.token) {
      token = window.token
    }
    if (process.env.NODE_ENV === 'development') {
      // admin uid=2 的永久token
      token =
        'eyJ0eXAiOiJKV1QiLCJhbGciOiJ' +
        'IUzI1NiJ9.eyJ1aWQiOjIsImV4c' +
        'CI6Mjg0MDExMjAwMCwicGxhdGZv' +
        'cm0iOiIifQ.x7sXrFHcDZUEiIkV' +
        'q2JDO1FQxpOsXXsq0qTaAKuGQNw'
    }
    return {
      action: process.env.VUE_APP_APPSERVER_HOST + '/admin/api.seckill.activity.add',
      importHeaders: {
        'X-ADMIN-Authorization': token,
        'X-HH-Ver': process.env.VUE_APP_APPSERVER_VERSION
      },
      hasFile: false,
      // 活动信息
      activity_infos: {
        secbuy_id: '',
        secbuy_name: '',
        start_time: '',
        end_time: ''
      },
      pageSize: 5,
      pageNumber: 1,
      currentPage: 1,
      total: 0,
      goods_info: [],
      // 导入信息提示
      data_type: {
        sale: '',
        goods_number: '',
        sale_goods_number: '',
        check_zero: '',
        export_fail: '',
        param_error: '',
        money_error: '',
        sku_goods: '',
        discount_activity_shop_price: '',
        special_activity_shop_price: '',
        special_activity_money_line: '',
        goods_name: '',
        other_activity_running: '',
        seckill_casg: '',
        seckill_price: '',
        shop_seckill_cash: '',
        shop_seckill_price: ''
      }, // 商品导入信息
      secbuy_infos: {}, // 活动信息
      // 校验规则
      addRules: {
        secbuy_name: [
          { required: true, message: '请输入活动名称', trigger: 'change' },
          { min: 3, max: 30, message: '活动名称长度3~30', trigger: 'change' }
        ],
        start_time: [
          { required: true, message: '请选择开始时间', trigger: 'blur' },
          { validator: checkStartDate, trigger: 'blur' }
        ],
        end_time: [
          { required: true, message: '请选择结束时间', trigger: 'blur' },
          { validator: checkEndDate, trigger: 'blur' }
        ]
      },
      fullscreenLoading: true,
      submitLoading: false,
      pageLoading: false,
      notSave: false,
      centerDialogVisible: false,
      mutex0: false,
      mutex1: false,
      ProductList: localStorage.getItem('productlist') ? JSON.parse(localStorage.getItem('productlist')) : []
    }
  },
  created() {
    // console.log(this.isAdd)
    if (!this.isAdd && !this.isProduct) {
      this.activity_infos.secbuy_id = this.$route.query.id
      this.getActivityInfos()
    }
    if (this.isAdd) {
      localStorage.clear()
      this.goods_info = this.ProductList = []
    }
    // console.log(this.ProductList)
    if (this.isProduct) {
      if (this.ProductList.length > 0) {
        // this.ProductList.secbuy_close_price = this.ProductList.shop_price
        // 加俩字段 秒杀现金结算价 = 秒杀现金价；秒杀积分结算价 = 秒杀积分支付上限
        this.ProductList.forEach(val => {
          val.secbuy_close_price = val.cash_price
          val.settlement_surplus = val.huanbi_price
        })

        localStorage.setItem('productlist', JSON.stringify(this.ProductList))
        this.goods_info = this.ProductList
        this.mutex0 = true
        this.mutex1 = true
        this.pageLoading = false
      }
    }
  },
  computed: {
    isAdd() {
      return this.$route.query.operate === 'add'
    },
    curStatus() {
      return Number(this.$route.query.status) === 1
    },
    isDisable() {
      return !this.isAdd && this.curStatus
    },
    isValid() {
      let flag = false
      this.$refs['activityForm'].validate(valid => {
        flag = valid
      })
      return flag
    },
    isLoading() {
      return this.fullscreenLoading && !this.isAdd && !this.isProduct
    },
    postParams() {
      let pageNumber = this.pageNumber,
        pageSize = this.pageSize
      var is_select, productlist, secbuy_status
      if (this.isProduct) {
        is_select = 1
        secbuy_status = 0
        productlist = localStorage.getItem('productlist')
      } else {
        is_select = 0
      }
      return { ...this.activity_infos, pageNumber, pageSize, is_select, productlist, secbuy_status }
    },
    isProduct() {
      return this.$route.query.operate === 'productlist'
    }
  },
  methods: {
    getActivityInfos() {
      this.pageLoading = true
      ActivityDetail(this.postParams).then(res => {
        this.activity_infos = res.secbuy_info
        this.activity_infos.start_time = this.utils.formatStamp(this.activity_infos.start_time, 1)
        this.activity_infos.end_time = this.utils.formatStamp(this.activity_infos.end_time, 1)
        this.goods_info = res.list
        this.fullscreenLoading = false
        this.pageLoading = false
        if (this.isProduct) {
          if (this.ProductList && this.ProductList.length > 0) {
            this.goods_info = this.ProductList
          }
        } else {
          this.total = res.total
        }
      })
    },
    // 页面切换
    handleCurrentChange() {
      console.log('current page is ', this.currentPage)
      this.pageNumber = this.currentPage
      this.getActivityInfos()
    },
    // 编辑商品
    goEdit(id) {
      this.$router.replace({ name: 'KillGoodEdit', params: { actID: this.activity_infos.secbuy_id, goodID: id } })
    },
    // 移除
    goRemove(id) {
      this.$confirm('确定移除该商品吗', '提示').then(() => {
        this.pageLoading = true
        // console.log(id)
        // 移除可售库存变动，调取java接口
        this.$api.delete(`hd/itzSecbuyGoods?secbuy_goods_id=${id}`, null, r => {
          if (r.status == 200) {
            RemoveGoods(id).then(res => {
              if (res) {
                console.log(res)
                this.handleCurrentChange()
              }
            })
          }
        })
      })
    },
    // 售罄
    sellNone(row) {
      SellGoodsNone(row.id).then(res => {
        if (res) {
          this.$message.success('该商品下架成功')
          row.is_on_sale = 0
        }
      })
    },
    // 保存活动
    saveNewActivity() {
      if (this.isValid) {
        // 新增必须导入商品
        if (this.isAdd) {
          if (!this.hasFile) {
            this.$message.error('新增活动必须导入商品')
          } else {
            this.submitLoading = true
            this.$refs.uploadFile.submit()
          }
        } else if (this.isProduct) {
          console.log(this.postParams)
          if (!this.postParams.productlist || this.postParams.productlist == '[]') {
            this.$message.error('新增活动必须导入商品')
          } else {
            this.submitLoading = false
            var listArr = JSON.parse(localStorage.getItem('productlist'))
            var kcArr = []
            listArr.forEach(val => {
              let pam = {}
              pam['quantity'] = val.secbuy_quantity
              pam['id'] = val.signup_id
              kcArr.push(pam)
            })
            console.log(kcArr)
            this.$api.put(`hd/itzSecbuyGoods`, kcArr, r => {
              if (r.status == 200) {
                console.log(this.postParams)
                AddNewActivity(this.postParams).then(res => {
                  if (res) {
                    console.log(res)
                    this.data_type = res.data.data_type
                    for (let key in this.data_type) {
                      if (this.data_type[key].length > 0) {
                        this.centerDialogVisible = true
                        this.submitLoading = false
                      }
                      this.data_type[key] = this.data_type[key].join()
                    }
                    this.total = res.total
                    if (res.list.length < 1) {
                      this.$confirm('保存失败,请移除问题商品之后重新保存', '提示', {
                        confirmButtonText: '确定',
                        type: 'warning'
                      })
                      return
                    } else {
                      this.submitLoading = false
                      this.$message.success('保存成功')
                      localStorage.clear()
                      this.mutex0 = false
                      this.mutex1 = false
                      this.submitLoading = false
                      this.goods_info = res.list
                      this.activity_infos = res.secbuy_info
                      this.activity_infos.start_time = this.utils.formatStamp(this.activity_infos.start_time, 1)
                      this.activity_infos.end_time = this.utils.formatStamp(this.activity_infos.end_time, 1)
                    }
                    // this.$router.replace({ name: 'SecondKill'})
                  }
                })
              }
            })
          }
        } else {
          // 编辑保存时，判断是否有重新导入商品
          if (new Date().getTime() >= this.activity_infos.start_time) {
            this.$message.error('活动已经开始了')
            return
          }
          if (this.hasFile) {
            this.submitLoading = true
            this.$refs.uploadFile.submit()
          } else {
            this.submitLoading = true
            AddNewActivity(this.postParams).then(res => {
              if (res) {
                this.$message.success('保存成功')
                this.submitLoading = false
              }
            })
          }
        }
      }
    },
    // 上传文件成功回调
    uploadSuccess(response) {
      this.submitLoading = false
      this.$refs.uploadFile.clearFiles()
      let _data = response.data
      this.data_type = _data.data.data_type
      for (let key in this.data_type) {
        if (this.data_type[key].length > 0) {
          this.centerDialogVisible = true
        }
        this.data_type[key] = this.data_type[key].join()
      }
      if (this.isAdd && !this.centerDialogVisible) {
        this.notSave = true
      }
      this.total = _data.total
      if (response.code || _data.list.length === 0) {
        this.$message.error('保存失败,请重新导入商品')
        this.hasFile = false
        return
      } else {
        this.$message.success('保存成功')
        this.goods_info = _data.list
        this.submitLoading = false
        this.activity_infos = _data.secbuy_info
        this.activity_infos.start_time = this.utils.formatStamp(this.activity_infos.start_time, 1)
        this.activity_infos.end_time = this.utils.formatStamp(this.activity_infos.end_time, 1)
      }
    },
    // 文件超出限制回调
    overFile() {
      this.$message.error('只能上传一个文件')
    },
    // 添加文件
    addFile(file) {
      // 添加文件、上传成功、失败都会调用该函数，只在添加文件时修改hasFile的状态
      if (file.status === 'ready') {
        this.hasFile = true
        this.mutex1 = false
        this.mutex0 = false
      }
    },
    // 移除文件
    removeFile() {
      this.hasFile = false
      this.mutex1 = false
      this.mutex0 = true
    },
    // 下载模板
    downloadTpl() {
      downloadTemplate().then(res => {
        saveAs(new Blob([res.data]), res.filename)
      })
    },
    // 钱格式化
    fmtMoney(row, column) {
      return this.utils.formatMoney(row[column.property])
    },
    // 支付方式转换
    fmtPayType(row, column) {
      let type = row[column.property]
      return PAYTYPE.map(function(item) {
        return item.type_id === type ? item.type_name : void 0
      })
    },
    //秒杀二期 线上选品功能  2019-10-8
    ChoiceOnline() {
      this.mutex1 = false
      this.mutex0 = true
      let id = this.$route.query.id ? this.$route.query.id : ''
      let status = this.$route.query.ststus ? this.$route.query.status : ''
      this.$router.push({ name: 'ChoiceOnline', query: { id: id, status: status } })
    },
    goback() {
      localStorage.clear()
      this.$router.replace({ name: 'SecondKill' })
    },
    Removepro(id) {
      let q = this.ProductList.findIndex(value => value.goods_id == id)
      this.ProductList.splice(q, 1)
      this.postParams.productlist = this.ProductList
      localStorage.setItem('productlist', JSON.stringify(this.ProductList))
      console.log(this.ProductList.length)
      if (this.ProductList.length < 1) {
        this.mutex1 = false
        this.mutex0 = false
      }
    }
  }
}
</script>

