// 显示抢购活动api
import { fetchEndpoint, fetchEndpointBlob } from '../server/network'

/**
 * 活动涉及到的接口
 */
// 活动新增 && 编辑
export const AddNewActivity = params =>
  fetchEndpoint('/admin/api.seckill.activity.add', 'POST', {
    secbuy_id: params.secbuy_id,
    secbuy_name: params.secbuy_name,
    start_time: params.start_time,
    end_time: params.end_time,
    pageNumber: params.pageNumber,
    pageSize: params.pageSize,
    is_select: params.is_select,
    secbuy_status: params.secbuy_status,
    data: params.productlist
    // myfile: params.myfile
  })

// 查看
export const ActivityDetail = params =>
  fetchEndpoint('/admin/api.seckill.activity.look', 'POST', {
    secbuy_id: params.secbuy_id,
    pageNumber: params.pageNumber,
    pageSize: params.pageSize
  })

// 禁用
export const ForbidActivity = id =>
  fetchEndpoint('/admin/api.seckill.activity.disable', 'POST', {
    secbuy_id: id
  })

// 开启
export const OpenActivity = id =>
  fetchEndpoint('/admin/api.seckill.activity.open', 'POST', {
    secbuy_id: id
  })

// 删除
export const DeleteActivity = id =>
  fetchEndpoint('/admin/api.seckill.activity.del', 'POST', {
    secbuy_id: id
  })

// 列表 && 搜索
export const ActivityList = params => fetchEndpoint('/admin/api.seckill.activity.find', 'POST', params)

/**
 * 商品涉及到的接口
 */
// 保存商品信息
export const SaveGoodsInfo = params => fetchEndpoint('/admin/api.seckill.goods.edit', 'POST', params)

// 移除商品
export const RemoveGoods = id =>
  fetchEndpoint('/admin/api.seckill.goods.del', 'POST', {
    id: id
  })

// 单商品编辑
export const GetGoods = id =>
  fetchEndpoint('/admin/api.seckill.goods.single', 'POST', {
    id: id
  })

// 商品售罄
export const SellGoodsNone = id =>
  fetchEndpoint('/admin/api.seckill.goods.sale', 'POST', {
    id: id
  })

// 保存，和秒杀参数有些区别，分开写
export const SaveNewActivity = params => fetchEndpoint('/admin/api.seckill.activity.add', 'POST', params)

// 下载模版
export const downloadTemplate = _ => fetchEndpointBlob('/admin/api.seckill.goods.template', 'POST')
